*,
*::before,
*::after {
  box-sizing: border-box;
}
h1 {
  text-align: center;
}
body {
  background-color: blueviolet;
  font-family: monospace;
  color: white;
  margin: 0;
}

.gifInput {
  display: flex;
  width: 50%;
}

input {
  border: none;
  height: 50px;
  display: inline-block;
  width: 800px;
  font-size: 20px;
  font-weight: bold;
  margin: auto;
  border-radius: 5px;
  font-family: monospace;
  outline: none;
}

.gifInput {
  text-align: center;
  color: black;
}

.gifsContainer {
  text-align: center;
  padding: 50px;
  display: flex;
  color: white;
  width: 100%;
  margin: 20px;
  background-color: gold;
  padding: 10px;
  font-family: Arial;
  justify-content: center;
  border-radius: 10px;
}
.carousel {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.carousel > ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.slide {
  position: absolute;
  inset: 0;
  opacity: 0;
}

.slide > img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.slide[data-active] {
  opacity: 1;
}
.carousel-button {
  position: absolute;
  z-index: 2;
  /* background: none; */
  border: none;
  font-size: 7rem;
  top: 50%;
  color: rgba(196, 13, 13, 0.814);
  border-radius: 0 0.25rem;
  padding: 0 0.5rem;
  background-color: rgba(0, 0, 0, 0.1);
  transform: translateY(-50%);
}

.carousel-button.prev {
  left: 1rem;
}

.carousel-button.next {
  right: 1rem;
}
